import { Component, ViewChild, ViewChildren, AfterViewInit } from '@angular/core';
import { Chart } from 'chart.js';
import { HTTP } from '@ionic-native/http/ngx';
import { NgxXml2jsonService } from 'ngx-xml2json';
import { BaseService } from '../services/base.service';
import { Constants, config, StatusCode } from '../data-model/constant.model';
import { DeviceModel, UsageOrImpressionModel, AccessTokenModel } from '../data-model/base.model';
import { Storage } from '@ionic/storage';
import * as moment from "moment";
import { IonSelect, AlertController } from '@ionic/angular';
import { LoadingController } from '@ionic/angular';
import { MatTabChangeEvent } from '@angular/material/tabs';
import { Observable, of, throwError } from 'rxjs';
import { forkJoin } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { stacked100 } from 'chartjs-plugin-stacked100';
import { Router } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { SelectMachineComponent } from '../shared/select-machine/select-machine.component';
import { BasePagePage } from '../shared/base-page/base-page.page';

require('chartjs-plugin-stacked100');

@Component({
  selector: 'impression',
  templateUrl: 'impression.page.html',
  styleUrls: ['impression.page.scss'],
})
export class ImpressionPage extends BasePagePage {

  @ViewChild('tabGroup') tabGroup;
  @ViewChild('popSelect') selectPop: IonSelect;

  strChartTitle: string = '';
  hrzBars5: any;
  arrDailyCharts: any[] = [];
  arrMonthlyCharts: any[] = [];
  arrYearlyCharts: any[] = [];
  customActionSheetOptions: any = {
    header: 'Machines',
    subHeader: 'Select the machine(s) you want to view'
  };
  arrMachines: Array<any> = [];
  arrSelectedMachines: Array<any> = [];
  selectedTab: any = 0;
  accessToken: string = '';
  deviceReqModel: DeviceModel = new AccessTokenModel();
  dailyReqModel: UsageOrImpressionModel = new UsageOrImpressionModel();
  monthlyReqModel: UsageOrImpressionModel = new UsageOrImpressionModel();
  yearlyReqModel: UsageOrImpressionModel = new UsageOrImpressionModel();

  currentDate: string = moment().format("YYYY-MM-DD");
  yesterdayDate: string = moment().add(-1, 'days').format("YYYY-MM-DD");
  minDate: string = Constants.c_MIN_DATE_2019;

  dataReturned: any;
  checkBoxList: Array<any> = [];

  constructor(
    public loadingCtrl: LoadingController,
    public alertCtrl: AlertController,
    private service: BaseService,
    private storage: Storage,
    public modalController: ModalController,
    private router: Router) {

    super(loadingCtrl, alertCtrl);

    this.strChartTitle = this.router.url.replace(/[^a-zA-Z0-9 ]/g, "");

    this.storage.get(Constants.c_ACCESS_TOKEN).then((accessToken) => {

      this.accessToken = accessToken;

      this.deviceReqModel.accesstoken = accessToken;

      this.dailyReqModel.accesstoken = accessToken;
      this.dailyReqModel.datefrom = this.standardDateTime(this.yesterdayDate);
      this.dailyReqModel.dateto = this.standardDateTime(this.yesterdayDate);

      this.monthlyReqModel.accesstoken = accessToken;
      this.monthlyReqModel.datefrom = this.standardDateTime(this.currentDate);
      this.monthlyReqModel.dateto = this.standardDateTime(this.currentDate);

      this.yearlyReqModel.accesstoken = accessToken;
      this.yearlyReqModel.datefrom = this.standardDateTime(this.currentDate);
      this.yearlyReqModel.dateto = this.standardDateTime(this.currentDate);

      this.presentLoading();

      this.service.callWebService(Constants.k_GET_DEVICE, this.deviceReqModel, (res) => {

        if (res.status_code == StatusCode.Success) {

          this.arrMachines = JSON.parse(res.data);

          this.arrMachines.forEach(element => {
            if (element === this.arrMachines[0]) {
              this.checkBoxList.push({ 'machine': element, 'isChecked': true })
            } else {
              this.checkBoxList.push({ 'machine': element, 'isChecked': false })
            }
          });

          this.arrSelectedMachines.push(this.arrMachines[0].value);

          this.dailyReqModel.device = this.arrSelectedMachines.join(",");
          this.monthlyReqModel.device = this.arrSelectedMachines.join(",");
          this.yearlyReqModel.device = this.arrSelectedMachines.join(",");

          this.callWSToReloadPagesData().subscribe(() => {

            this.dismissLoading();

          });

        } else {

          this.dismissLoading();

        }

      });

    });

  }

  ionViewDidEnter() {

  }

  callWSToReloadPagesData(): Observable<any> {

    let response1 = this.service.callWebService(Constants.k_DAILY_IMP, this.dailyReqModel, (res) => {
      this.arrDailyCharts = JSON.parse(res.data);
    });
    let response2 = this.service.callWebService(Constants.k_MONTHLY_IMP, this.monthlyReqModel, (res) => {
      this.arrMonthlyCharts = JSON.parse(res.data);
    });
    let response3 = this.service.callWebService(Constants.k_YEARLY_IMP, this.yearlyReqModel, (res) => {
      this.arrYearlyCharts = JSON.parse(res.data);
    });

    return forkJoin([response1, response2, response3]);

  }

  onFromDailyUsageSelected(item: any) {
    let from = Date.parse(this.dailyReqModel.datefrom)
    let to = Date.parse(this.dailyReqModel.dateto)
    if (from > to) {
      this.dailyReqModel.dateto = this.dailyReqModel.datefrom
    }
  }

  onToDailyUsageSelected(item: any) {
    let from = Date.parse(this.dailyReqModel.datefrom)
    let to = Date.parse(this.dailyReqModel.dateto)
    if (to < from) {
      this.dailyReqModel.datefrom = this.dailyReqModel.dateto
    }
  }

  onFromMonthlyUsageSelected(item: any) {
    let from = Date.parse(this.monthlyReqModel.datefrom)
    let to = Date.parse(this.monthlyReqModel.dateto)
    if (from > to) {
      this.monthlyReqModel.dateto = this.monthlyReqModel.datefrom
    }
  }

  onToMonthlyUsageSelected(item: any) {
    let from = Date.parse(this.monthlyReqModel.datefrom)
    let to = Date.parse(this.monthlyReqModel.dateto)
    if (to < from) {
      this.monthlyReqModel.datefrom = this.monthlyReqModel.dateto
    }
  }

  onFromYearlyUsageSelected(item: any) {
    let from = Date.parse(this.yearlyReqModel.datefrom)
    let to = Date.parse(this.yearlyReqModel.dateto)
    if (from > to) {
      this.yearlyReqModel.dateto = this.yearlyReqModel.datefrom
    }
  }

  onToYearlyUsageSelected(item: any) {
    let from = Date.parse(this.yearlyReqModel.datefrom)
    let to = Date.parse(this.yearlyReqModel.dateto)
    if (to < from) {
      this.yearlyReqModel.datefrom = this.yearlyReqModel.dateto
    }
  }

  selectMachine() {

    // this.selectPop.open()

    // this.service.getWebService(Constants.k_GET_DEVICE, this.deviceReqModel, (res) => {

    //   this.arrMachines = JSON.parse(res.data);

    //   // Use a timeout to gice some time to Angular
    //   // to update the view. The amount of time is not
    //   // important; it's just to run the code async
    //   setTimeout(() => {

    //     // Open the select
    //     this.selectPop.open()

    //   }, 100);

    // });

    this.presentModal()

  }

  optionsFn() {

    config.log("item: ", this.arrSelectedMachines)

    this.dailyReqModel.device = this.arrSelectedMachines.join(",");
    this.monthlyReqModel.device = this.arrSelectedMachines.join(",");
    this.yearlyReqModel.device = this.arrSelectedMachines.join(",");

    this.presentLoading();
    this.callWSToReloadPagesData().subscribe(() => {
      this.dismissLoading();
    });

  }

  onTabChanged(tabChangeEvent: MatTabChangeEvent): void {
    config.log('tabChangeEvent => ', tabChangeEvent);
    config.log('index => ', tabChangeEvent.index);
    // this.optionsFn(null)
  }

  onViewDailyDateClicked() {

    this.dailyReqModel.datefrom = this.standardDateTime(this.dailyReqModel.datefrom);
    this.dailyReqModel.dateto = this.standardDateTime(this.dailyReqModel.dateto);

    this.presentLoading();
    this.service.callWebService(Constants.k_DAILY_IMP, this.dailyReqModel, (res) => {
      this.arrDailyCharts = JSON.parse(res.data);
      this.dismissLoading();
    });

  }

  onViewMonthlyDateClicked() {

    this.monthlyReqModel.datefrom = this.standardDateTime(this.monthlyReqModel.datefrom);
    this.monthlyReqModel.dateto = this.standardDateTime(this.monthlyReqModel.dateto);

    this.presentLoading();
    this.service.callWebService(Constants.k_MONTHLY_IMP, this.monthlyReqModel, (res) => {
      this.arrMonthlyCharts = JSON.parse(res.data);
      this.dismissLoading();
    });

  }

  onViewYearlyDateClicked() {

    this.yearlyReqModel.datefrom = this.standardDateTime(this.yearlyReqModel.datefrom);
    this.yearlyReqModel.dateto = this.standardDateTime(this.yearlyReqModel.dateto);

    this.presentLoading();
    this.service.callWebService(Constants.k_YEARLY_IMP, this.yearlyReqModel, (res) => {
      this.arrYearlyCharts = JSON.parse(res.data);
      this.dismissLoading();
    });

  }

  random_rgb() {
    var o = Math.round, r = Math.random, s = 255;
    return 'rgb(' + o(r() * s) + ',' + o(r() * s) + ',' + o(r() * s) + ',' + r().toFixed(1) + ')';
  }

  standardDateTime(dateString: string) {
    return moment(dateString).format("YYYY-MM-DD");
  }

  async presentModal() {
    const modal = await this.modalController.create({
      component: SelectMachineComponent,
      componentProps: {
        "checkBoxList": this.checkBoxList,
        "paramTitle": "Select Machine"
      }
    });

    modal.onDidDismiss().then((dataReturned) => {
      if (dataReturned !== null && dataReturned.data !== undefined) {
        // this.dataReturned = dataReturned.data;

        this.arrSelectedMachines = [];

        dataReturned.data.forEach(element => {
          if (element.isChecked) {
            this.arrSelectedMachines.push(element.machine.value)
          }
        });
        config.log('--- Modal SELECTED Data: ', this.arrSelectedMachines);

        this.optionsFn();

      }
    });

    return await modal.present();
  }


}
